:root {
  --color-calculator: black;
  --color-screen: green;
  --color-button: lightgray;
  --color-button-operators: gray;
  --color-button-clear: red;
  --color-button-equals: blue;
  --color-keyboard: white;
  --color-background: aqua;
  --color-text-display: white;
  --color-text-formula: orange;
  --color-text-buttons: white;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--color-background);
  display: flex;
  align-items: center;
  justify-content: center;
}

#calculator {
  color: var(--color-text-buttons);
  background-color: var(--color-calculator);
  padding: 10px;
  max-width: 500px;
  width: 90vw;
  height: fit-content;
}
#calculator #calcName {
  text-align: center;
}
#calculator #screen {
  font-size: 2rem;
  background-color: var(--color-screen);
  margin-bottom: 10px;
  height: 20vh;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 0px 10px;
  text-align: right;
}
#calculator #screen #formula {
  color: var(--color-text-formula);
}
#calculator #screen #display {
  color: var(--color-text-display);
}
#calculator #keyboard {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  height: 60vh;
}
#calculator #keyboard .button {
  font-size: 3rem;
  background-color: var(--color-button);
  margin: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
}
#calculator #keyboard #clear,
#calculator #keyboard #zero {
  grid-column: span 2;
}
#calculator #keyboard #clear {
  background-color: var(--color-button-clear);
}
#calculator #keyboard #equals {
  grid-row: span 2;
  background-color: var(--color-button-equals);
}
#calculator #keyboard .operator {
  background-color: var(--color-button-operators);
}

#debugStateDisplayer {
  display: none;
}